<template>
    <div class="container">
      <div class="box">
        <h1>Web Maze</h1>
        <p>一个尝试，参考了<a href="https://nazo.one-story.cn/">Nazo Game</a>以及曾经一位学长做的Web Maze。</p>
        <p>你需要做的就是进入新的一关，如果你玩过Nazo Game，那么游玩本游戏会变得很简单。</p>
        <p>建议使用PC进行游戏(Chrome最佳)。</p>
        <p>答案是<b>大小写敏感的</b>，并且有些需要你<b>手动输入网址栏</b>以进入下一关。</p>
        <p>准备好了的话，就可以点击下面的按钮开始了。</p>
        <button @click="startGame" class="start-button">开始</button>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'HelloPage',
    methods: {
        startGame(){
            this.$router.push("/start");
        }
    }
  }
  </script>
  
  <style src="../assets/style.css" scoped></style>


