<template>
    <div class="container">
        <div class="box">
            <h1>Level 9</h1>
            <p>你是CV战士吗？</p>
            <p>C. 我觉得我是&emsp;&emsp;&emsp;V. 我觉得我是</p>
        </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'Level9',
    mounted() {
      // 在组件挂载后添加键盘事件监听器
      document.addEventListener('keydown', this.handleKeyDown);
    },
    beforeDestroy() {
      // 在组件销毁前移除键盘事件监听器，以防止内存泄漏
      document.removeEventListener('keydown', this.handleKeyDown);
    },
    data(){
      return {
        ctrlPressed: false,
        cPressed: false,
        vPressed: false,
        others: false
      }
    },
    methods: {
      handleKeyDown(e){
        if(e.key == 'Control'){
          this.ctrlPressed = true;
        }else if(e.key == 'c'){
          this.cPressed = true;
        }else if(e.key == 'v'){
          this.vPressed = true;
        }else{
          this.others = true;
        }

        if(this.ctrlPressed && this.cPressed && this.vPressed){
          this.$router.push("/brainfuck");
        }
        if(this.others){
          this.ctrlPressed = false;
          this.cPressed = false;
          this.vPressed = false;
          this.others = false;
        }
      }
    }
  }
  </script>
  
  <style src="../assets/style.css" scoped></style>
  


