import Vue from 'vue'
import Router from 'vue-router'
import HelloPage from '../views/HelloPage.vue'
import Level1 from '../views/Level1.vue'
import Level2 from '../views/Level2.vue'
import Level3 from '../views/Level3.vue'
import Level404 from '../views/Level404.vue'
import Level4 from '../views/Level4.vue'
import Level5 from '../views/Level5.vue'
import Level6 from '../views/Level6.vue'
import Level7 from '../views/Level7.vue'
import Level8 from '../views/Level8.vue'
import Level9 from '../views/Level9.vue'
import Level10 from '../views/Level10.vue'
import EndPage from '../views/EndPage.vue'
import SPPage from '../views/SPPage.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloPage',
      component: HelloPage
    },
    {
      path: '/start',
      name: 'Level1',
      component: Level1
    },
    {
      path: '/level2',
      name: 'Level2',
      component: Level2,
    },
    {
      path: '/level3',
      name: 'Level3',
      component: Level3
    },
    {
      path: '/suda',
      name: 'Level4',
      component: Level4
    },
    {
      path: '/answer10',
      name: 'Level5',
      component: Level5
    },
    {
      path: '/a_A_0',
      name: 'Level6',
      component: Level6
    },
    {
      path: '/ARM',
      name: 'Level7',
      component: Level7
    },
    {
      path: '/82ff5c',
      name: 'Level8',
      component: Level8
    },
    {
      path: '/DhwM',
      name: 'Level9',
      component: Level9
    },
    {
      path: '/brainfuck',
      name: 'Level10',
      component: Level10
    },

    {
      path: '/finish',
      name: 'EndPage',
      component: EndPage
    },
    {
      path: '/forspyou',
      name: 'SPPage',
      component: SPPage
    },

    {
      path: '/level404',
      name: 'Level404',
      component: Level404
    },
    {
      path: '*',
      redirect: '/level404'
    }
  ]
})



