<template>
    <div class="container">
        <div class="box">
            <h1>Level 1</h1>
            <p>请输入答案</p>
            <p>
                <input type="text" placeholder="" @input="onChange" class="underline-input">
            </p>
        </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'Level1',
    methods: {
        onChange(e){
            console.log(e.target.value);
            if(e.target.value == "答案"){
                this.$router.push("/level2");
            }
        }
    }
  }
  </script>
  
  <style src="../assets/style.css" scoped></style>
  


